
<style lang="less" scoped>
  #work_header{
    position: sticky;
    top: 0;
    z-index: 1;
  }
  section.header {
    background-image: url('https://image.dapengjiaoyu.com/2018/08/jl1v9zxg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 300px;
    display: flex;
    position: relative;
    background-color: #fff;
    &>img {
      position: absolute;
      top: 50%;
      left: 4%;
      transform: translateY(-50%);
      width: 50px;
      height: 60px;
    }
    &>div {
      text-align: center;
      flex: 5;
      height: 300px;
      padding-top: 80px;
      box-sizing: border-box;
      &>p {
        color: #fff;
        span {
          color: #fff;
        }
      }
    }
  }
  .last_time {
    opacity: .5;
    font-size: 27px !important;
    color: #fff;

  }
  .course_content>p{
    font-size: 35px;
    color: #fff;
    &:nth-child(1){
      margin-bottom: 20px;
    }
  }
  .day_time {
    font-size: 35px;
    color: #fff;
  } // 切换栏 START
  .change_tab {
    display: flex;
    height: 90px;
    background-color: #fff;
    &>div {
      position: relative;
      flex: 5;
      text-align: center;
      height: 90px;
      line-height: 90px;
      font-size: 25px;
    }
  } // 切换栏 END
  div.active {
    color: #0dc04a;
  }
  div.active::after {
    content: '';
    display: block;
    width:  100px;
    height: 5px;
    position: absolute;
    background-color: #0dc04a;
    bottom: 0;
    color: #0dc04a;
    left: 50%;
    transform: translateX(-50%);
  }
</style>

<template>
  <div>
    <div id="work_header">
      <section class="header">
        <img src="../../assets/img/per_fh.png" alt="返回" @click="back" class="back last_time">
        <div>
          <div class="course_content">
            <p class="last_time">上次学习时间</p>
            <p><span class="day_time">{{day.studyTtime}}</span> 天前</p>
          </div>
        </div>
        <div>
          <div class="course_content">
            <p class="last_time">你已经在大鹏学习了</p>
            <p><span class="day_time">{{day.studyDay}}</span> 天</p>
          </div>
        </div>
      </section>
      <section class="change_tab">
        <router-link tag="div" to="/opended">已开通课程</router-link>
        <router-link tag="div" to="/collected">已收藏课程</router-link>
      </section>
    </div>
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        day: {
          studyTtime: null,
          studyDay: null
        }
      }
    },
    methods: {
      back() {
        this.$router.push({
          path: '/mine'
        });
      }
    },
    components: {},
    mounted() {
      this.axios.post('/api/studyTtime').then((data) => {
        this.day = data.data.result;
      }).catch((err) => {
        console.log(err);
      });
    }
  }
</script>



